.detectionLive-info .detectionLive-card{
    width: 100%;
    overflow: hidden;
    transition: all 0.3s ease-in-out;
}
.detectionLive-info:first-child{
    margin-top: 22px;
}

/* body */
.detectionLive-card-body{
    height:37px;
    padding: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.detectionLive-card-body span{
    width: 100px;
    text-align: right;
    margin-right: 15px;
    flex-shrink: 0;
}

.detectionLive-card-body .bounding-box-process>p{
    width: 128px;
    height: 2px;
    border-radius: 1px;
    background-color: #02D4FA;
}
.detectionLive-card-body .bounding-box-process>input{
    width: 40px;
    height: 30px;
    border-radius: 1px;
    background-color: #00447F ;
    border: none;
    margin-left: 5px;
    color: #fff;
    text-align: center;
    outline: none;
}
.opacity{
    position:absolute;
    top:0;
    /* left: 0; */
    width:20px!important;
    height:20px;
    border-radius:50%;
    background-color:#18EAF3;
    transform:translateY(-50%);
    margin: 0!important;
}
</style>